.route-detail {
  flex-grow: 1;
  margin-top: 30px;
  overflow: hidden;

  &__body {
    padding: 30px 0;
    max-height: 100%;
    box-sizing: border-box;
  }

  &__list {
    margin: 0 150px;
    border-left: 8px solid #084ac4;
  }

  &__item {
    position: relative;
    padding: 10px 50px;
    color: var(--c-black);
    font-size: 30px;
    font-weight: 400;

    &::before {
      content: '';
      box-sizing: border-box;
      position: absolute;
      top: 50%;
      left: -20px;
      transform: translateY(-50%);
      width: 32px;
      height: 32px;
      border-radius: 50%;
      border: 5px solid #ffffff;
      background-color: #084ac4;
    }

    &.b-active {
      color: #084ac4;
      font-size: 35px;
      font-weight: 700;
    }

    &:first-child,
    &:last-child {
      color: #084ac4;
      font-size: 45px;
      font-weight: 700;

      &::before {
        left: -36px;
        width: 64px;
        height: 64px;
        border: 8px solid #ffffff;
        background-color: #084ac4;
      }
    }

    &:first-child {
      position: relative;
      top: -20px;
    }

    &:last-child {
      position: relative;
      bottom: -20px;
    }
  }
}
